import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { WebView } from "@ionic-native/ionic-webview/ngx";
import { Platform } from "@ionic/angular";
import { ApiService } from "src/app/services/api.service";
import { ContentService } from "src/app/services/content.service";
import gsap from "gsap";
import { DomSanitizer, SafeResourceUrl } from "@angular/platform-browser";
import { InAppBrowser } from "@ionic-native/in-app-browser/ngx";

@Component({
  selector: "app-embed",
  templateUrl: "./embed.page.html",
  styleUrls: ["./embed.page.scss"],
})
export class EmbedPage implements OnInit {
  public openedContent;
  public openedSong;
  public toembed: SafeResourceUrl = "";
  public contentPath;
  public contentSetting;
  public songlist;
  public cover;
  constructor(
    public contentService: ContentService,
    private api: ApiService,
    private platform: Platform,
    private webview: WebView,
    private router: Router,
    private domSanitizer: DomSanitizer,
    private iab: InAppBrowser
  ) {
    this.openedContent = this.contentService.openedContent;
    this.openedSong = this.contentService.openedSong;
    this.contentPath = this.contentService.path;
    if (this.contentPath !== "" && !this.platform.is("electron")) {
      this.contentPath = this.webview.convertFileSrc(
        this.contentService.path + this.openedContent.dirname + "/"
      );
    }
    if (this.contentPath !== "" && this.platform.is("electron")) {
      this.contentPath =
        this.contentService.path + this.openedContent.dirname + "/";
    }
  }
  async ngOnInit() {
    this.init();
  }
  async init() {
    if (this.contentPath === "") {
      if (this.openedContent.stream_location !== "") {
        this.contentPath = this.openedContent.stream_location;
      } else {
        this.contentPath = this.openedContent.download_location;
      }
    }
    await this.getSetting();
    await this.getSonglist();
    console.log(this.contentSetting.graphic);
    let wp;
    wp = document.getElementById("wrapperr1");
    if (this.contentSetting.graphic !== "") {
      let ext =
        this.platform.is("ios") && !this.platform.is("mobileweb")
          ? "." + this.contentSetting.graphic.split(".").pop()
          : "";
      wp.style.cssText =
        "background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),url('" +
        (
          this.contentPath +
          btoa(this.openedContent.dirname + "/" + this.contentSetting.graphic) +
          ext
        )
          .replace("file:\\", "file://")
          .replace(/\\/g, "/") +
        "');background-position:center center;background-size:cover;";
    } else {
      wp.style.cssText =
        "background: " + this.contentSetting.backgroundColor + ";";
    }
    let x =
      this.platform.is("ios") && !this.platform.is("mobileweb")
        ? "." + this.openedContent.cover.split(".").pop()
        : "";
    this.cover = this.contentPath + this.openedContent.cover + x;
    gsap.from(".animated", 1, { opacity: 0, scale: 0 });
    setTimeout(() => {
      this.hideLoading();
    }, 1000);
  }

  hideLoading() {
    // document.getElementById("loadigg").style.display = "none";
    gsap.to("#loadigg1", 0.3, { scale: 0, opacity: 0 });
    gsap.from(".coverr", 0.3, { scale: 0, delay: 0.3 });
    gsap.from("#title1", 0.3, { opacity: 0, delay: 0.4 });
    gsap.from(".item", 0.3, { opacity: 0, stagger: 0.1, delay: 0.4 });
  }
  async getSetting() {
    let response = await this.contentService
      .getJson("settings/settings.json", this.contentPath)
      .toPromise();
    if (response) {
      this.contentSetting = response;
    }
  }
  async getSonglist() {
    let songData = await this.contentService
      .getJson(`songlist/${this.openedSong.url}/embed.json`, this.contentPath)
      .toPromise();
    if (songData) {
      this.songlist = songData;
    }
  }
  openUrl(url, location) {
    if (location == "inside") {
      this.toembed = this.domSanitizer.bypassSecurityTrustResourceUrl(url);
    } else {
      this.iab.create(url, "_blank");
    }
  }
  back() {
    window.history.back();
  }
  close() {
    this.toembed = "";
  }
}
